<template>
    <div class="col-md-6 swap-second-div ng-scope" ng-controller="loginCtrl" style="width: 100%;">
   <div class="loginsec__right">
      <h2 class="gradientHead head_mob_size" style="text-align: center;margin-top:0px">User Registration Form-Red Lotus</h2>
      <div class="row">
         <div class="col-lg-11">
            <form method="post" @submit.prevent="onSubmit" style="width: 100%;" class="ng-pristine ng-valid">
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">First Name</label> </div>
                  <div class="common_input"> <input type="text" name="fname" style=" height: 10px;color:#c526aa; "> </div>
               </div>
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Last Name</label> </div>
                  <div class="common_input"> <input type="text" name="lname" style=" height: 10px; color:#c526aa;"> </div>
               </div>
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Date of Birth</label> </div>
                  <div class=""> <input type="date" name="dob" style=" width: 100%; height: 40px; border-color: white; color:#c526aa;"> </div>
               </div>
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Email Address</label> </div>
                  <div class="common_input"> <input type="email" name="email" style=" height: 10px; color:#c526aa;"> </div>
               </div>              
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Username</label> </div>
                  <div class="common_input"> <input type="text" name="username" required="required" style="height: 10px;color:#c526aa;"> </div>
               </div>
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Password</label> </div>
                  <div class="common_input"> <input type="password" name="password1" required="required" style=" height: 10px; color:#c526aa;"> </div>
               </div>
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Confirm Password</label> </div>
                  <div class="common_input"> <input type="password" name="password2" required="required" style=" height: 10px; color:#c526aa;"> </div>
               </div>
               <div class="col-lg-6">
                  <div> <label class="head_mob_size">Security Key</label> </div>
                  <div class="common_input"> <input type="password" name="key" required="required" style=" height: 10px; color:#c526aa;"> </div>
               </div>
               <input type="submit" name="btnSubmit" value="Create User"> 
            </form>
         </div>
      </div>
   </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
   name: 'register',
   components: {
      
   },
   methods: {
         onSubmit(submitEvent) {
            if(submitEvent.target.elements.key.value == 'KalaiKalaiKalai'){
               var password;
               var fname = submitEvent.target.elements.fname.value;
               var lname = submitEvent.target.elements.lname.value;
               var dob = submitEvent.target.elements.dob.value;
               var email = submitEvent.target.elements.email.value;
               var username = submitEvent.target.elements.username.value;
               var password1 = submitEvent.target.elements.password1.value;
               var password2 = submitEvent.target.elements.password2.value;
               if(password1 == password2){
                  password = password1;
                  axios.post('admin/createUser', {
                     fname,
                     lname,
                     dob,
                     email,
                     username,
                     password
                  })
                  .then(function (response) {
                     console.log('response', response);
                     window.location.href = '/';
                     alert('User Added Successfully');
                  })
                  .catch(function (error) {
                     console.log('error', error);
                  });
               } else {
                  alert('password mismatch');
               }
            } else {
               alert('Security Key mismatch');
            }
            
         }
   }
}

</script>
<style>

</style>